/* Fonts settings */
@font-face {
  font-family: 'ProximaNova_bold';
  src: url('../fonts/ProximaNova-Bold.eot');
  src: url('../fonts/ProximaNova-Bold.eot?#iefix') format('embedded-opentype'), url('../fonts/ProximaNova-Bold.woff2') format('woff2'), url('../fonts/ProximaNova-Bold.woff') format('woff'), url('../fonts/ProximaNova-Bold.ttf') format('truetype'), url('../fonts/ProximaNova-Bold.svg#ProximaNova-Bold') format('svg');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'ProximaNova_rg';
  src: url('../fonts/ProximaNova-Regular.eot');
  src: url('../fonts/ProximaNova-Regular.eot?#iefix') format('embedded-opentype'), url('../fonts/ProximaNova-Regular.woff2') format('woff2'), url('../fonts/ProximaNova-Regular.woff') format('woff'), url('../fonts/ProximaNova-Regular.ttf') format('truetype'), url('../fonts/ProximaNova-Regular.svg#ProximaNova-Regular') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
/*Colors*/
body {
  font-family: 'ProximaNova_rg', sans-serif;
  color: #1a1820;
}
button:focus,
a:focus,
input:focus {
  outline: 0;
}
a:hover {
  text-decoration: none;
}
.btn {
  padding: 20px 30px;
  border-radius: 8px;
  font-size: 13px;
  background-color: #f8b001;
  color: #fff;
  text-transform: uppercase;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.btn:hover {
  color: #fff;
  cursor: pointer;
  background-color: #d49701;
}
section {
  padding: 100px 0 ;
}
.header {
  height: 70px;
}
.wrap {
  display: table;
  width: 100%;
  height: 70px;
  text-align: right;
}
.wrap-block {
  display: table-cell;
  vertical-align: middle;
}
.phone-link-block {
  text-align: right;
}
.phone-link {
  display: inline-block;
  color: #1a1820;
  font-family: 'ProximaNova_bold', sans-serif;
  font-size: 18px;
  height: 70px;
  line-height: 70px;
  text-align: right;
}
.main {
  background: #ffffff url(../img/main-layer.jpg) center center no-repeat;
  background-size: cover;
  padding: 30px 0;
}
.main-content {
  margin-top: 100px;
}
.main-content h1 {
  font-size: 40px;
  font-family: 'ProximaNova_bold', sans-serif;
  line-height: 58px;
  text-transform: uppercase;
}
.main-content-subtitle {
  margin-top: 50px;
  font-size: 24px;
  margin-bottom: 60px;
}
.main-content-block {
  display: inline-block;
  padding: 20px  40px 0px 40px;
  font-size: 18px;
  background: #70c849;
  color: #fff;
  text-align: center;
}
.main-content-block span {
  font-size: 30px;
  font-family: 'ProximaNova_bold', sans-serif;
}
.main-content-after {
  margin-top: 35px;
  font-size: 22px;
  font-family: 'ProximaNova_bold', sans-serif;
  line-height: 58px;
  text-transform: uppercase;
}
.main-form {
  display: block;
  height: 405px;
  background: #fff;
  padding: 35px 45px;
  text-align: center;
}
.main-form h2 {
  font-size: 18px;
  margin-bottom: 35px;
}
.main-form-input {
  display: block;
  width: 100%;
  height: 54px;
  border-radius: 5px;
  border: 1px solid #dddddd;
  margin-bottom: 20px;
}
.main-form-btn {
  display: block;
  width: 100%;
  font-size: 16px;
  font-family: 'ProximaNova_bold', sans-serif;
  text-transform: uppercase;
}
.main-form label {
  position: absolute;
  left: 50%;
  font-size: 13px;
  padding: 5px;
  background-color: #fff;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  font-family: 'ProximaNova_bold', sans-serif;
}
.main-form small {
  display: block;
  margin-top: 15px;
  font-size: 12px;
}
.main-reference {
  margin-top: 35px;
  padding-left: 30px;
  padding-right: 110px;
  font-size: 12px;
}
/*media requests*/
@media (max-width: 991px) {
  .main {
    background-position: top right;
  }
  .phone-link-block {
    text-align: center;
  }
  .main-content {
    margin-top: 40px;
  }
  .main-title {
    text-align: center;
  }
}
@media (max-width: 767px) {
  .phone-link-block {
    text-align: right;
    margin-left: 10px;
    padding-right: 0;
  }
}
@media (max-width: 575px) {
  .main {
    background-position: left;
  }
  .header,
  .phone-link-block,
  .wrap {
    text-align: center;
  }
  .main-content {
    margin-top: 150px;
  }
  .main-content-after {
    margin-top: 5px;
  }
  .main-content h1 {
    font-size: 30px;
  }
}
